import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Card, Row, Table, Col } from 'antd';
import { importpoint } from 'dump/data';
import FormSearch from './FormSearch';

const columns = [
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'sbd',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dateOfBirth',
    key: 'birthdate',
  },
  {
    title: 'Điểm chuyên cần',
    dataIndex: 'dcn',
    key: 'dcn',
  },
  {
    title: 'Kiểm tra định kỳ',
    dataIndex: 'ktdk',
    key: 'dateTS',
  },
  {
    title: 'Thi',
    dataIndex: 'thi',
    key: 'tdtb',
  },
];

class ImportPointExcel extends Component {
  render() {
    return (
      <Card title="Nhập khẩu dữ liệu">
        <Row>
          <Col span={24}>
            <FormSearch />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={importpoint} columns={columns} />
          </Col>
          <Col offset={8} span={12}>
            <BtnSave htmlType="submit" />
            <BtnCancel onClick={this.cancelUpload} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ImportPointExcel />
  </Page>
);



