import React, { Component } from 'react';
import { Row, Col, Form, Input, Upload, Icon, Button } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { BtnSearch, BtnClear } from 'components/common/button';

@Form.create({ name: 'FormSearch' })
class FormSearch extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={8}>
            <Form.Item label="Học kỳ">{getFieldDecorator('name', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Năm học">{getFieldDecorator('name', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Học phần">{getFieldDecorator('name', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="HK nhập điểm">{getFieldDecorator('name', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Năm nhập điểm">{getFieldDecorator('name', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Lần thi">{getFieldDecorator('name', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="File dữ liệu">
              {getFieldDecorator('upload', {
                valuePropName: 'fileList',
                getValueFromEvent: this.normFile,
              })(
                <Upload name="file" listType="picture">
                  <Button>
                    <Icon type="upload" /> Click to upload
                  </Button>
                </Upload>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearch;



